.row {
  display: flex;
  justify-content: center;
}
.one {
  align-items: flex-end;
}
.two {
  align-items: center;
}
.three {
  align-items: flex-start;
}
#start-btn {
  display: inline-block;
  outline: none;
  cursor: pointer;
  font-weight: 600;
  border-radius: 3px;
  padding: 12px 24px;
  border: 0;
  color: #000021;
  background: #1de9b6;
  line-height: 1.15;
  font-size: 20px;
}

#start-btn :hover {
  transition: all 0.5s ease;
  box-shadow: 0 0 0 0 #fff, 0 0 0 3px #1de9b6;
}

.boxy {
  opacity: 0;
  transition: opacity 0.7s ease-in-out;
}

.boxy.show {
  opacity: 1;
}
